@charset "utf-8";

header{
  padding: 20px 0;
  text-align: center;
  box-shadow: 0px 3px 3px rgba(0,0,0,0.15);
  width: 100%;
  height: 30px;
  background-color: #fff;
  position: fixed;
  left: 0px;top:0px;
}
header h1 img{width: 40px;}
main{padding: 15px 10px; margin-top: 130px;}
footer{
  clear: both;
  background-color: var(--base_color);
  color: #fff;
  text-align: center;
  line-height: 40px;
}
footer > .top_btn{
  font-size: 20px;
  position: fixed;
  bottom: 10%; right: 5%;
  border-radius: 50%;
  width: 50px; height: 50px;
  border: 1px #ccc;
  background-color: #006633;
  color: #fff;
  cursor: pointer;
  box-shadow: 0px 3px 3px rgba(0,0,0,0.15);
}
footer > address{font-style:normal;}