/* main{width: 100%;} */
main > h2{
  font-size: 22px;
  font-weight: 600;
  text-align: center;
}

/* input */
.input_item{
  width: 94%;max-width: 1200px;
  margin: 0 auto;
}
.input_item > p{padding: 5px 0;}
.input_item > p > label{font-size: 16px;line-height: 30px;}
.input_item > p > input{
  width: 100%; display: block;
  height: 40px;
  border-radius: 5px;
  border: none;
  text-indent: 10px;
  background: #ccc;
}
.input_item button{
  width: 100%;
  height: 40px;
  border: none;
  color: #fff;
  background: #006633;
  cursor: pointer;
  border-radius: 5px;
  margin: 10px 0;
}

.products_list{
  width: 94%;
  max-width: 1200px;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 20px;
  margin: 0 auto;
}
.products_list > li{}
.products_list > li img{width: 100%; border-radius: 20px;}

.products_list > li .pname{
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  margin: 10px 0;
}
.products_list > li .pprice{
  text-align: right;
  color: #006633;
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 10px;
}
.products_list > li button{
  padding: 5px 15px;
  color: #fff;
  background-color: #006633;
  border: none;
  border-radius: 5px;
  margin-bottom: 20px;
}

